.formTitle {
   font-weight: normal;
   font-size  : 36px;
   line-height: 150%;
   color      : #FFFFFF;
   align-self : flex-start;
   margin     : 20px 0 40px 0;
}

.form {
   & * {
      box-sizing: border-box;
   }

   width          : 100%;
   display        : flex;
   flex-direction : column;
   align-items    : center;
   justify-content: space-between;

   .row1 {
      width          : 100%;
      display        : flex;
      flex-direction : row;
      align-items    : center;
      justify-content: space-between;
   }

   .row2 {
      margin-bottom: 25px;
   }

   .col1,
   .col2 {
      width          : 48%;
      height         : 400px;
      display        : flex;
      flex-direction : column;
      align-items    : flex-start;
      justify-content: space-between;
      margin-bottom  : 40px;
   }

   input:not([type='file']) {
      width           : 100%;
      height          : 48px;
      padding         : 10px 20px;
      color           : #fff;
      background-color: transparent;
      border          : 2px solid #fff;
      outline         : none;
      border-radius   : 24px;
      margin-bottom   : 30px;
      font-weight     : normal;
      font-size       : 16px;
      line-height     : 150%;
      color           : #FFFFFF;

      &::placeholder {
         color: inherit;
      }
   }

   textarea {
      height       : 100%;
      width        : 100%;
      background   : transparent;
      outline      : none;
      border       : 2px solid #fff;
      border-radius: 24px;
      padding      : 15px;
      font-weight  : normal;
      font-size    : 16px;
      line-height  : 150%;
      color        : #FFFFFF;

      &::placeholder {
         color: inherit;
      }
   }

   #budget {
      margin: 0 0 40px 0;
   }

   .title {
      font-weight: normal;
      font-size  : 16px;
      line-height: 150%;
      color      : #FFFFFF;
      margin     : 0 0 5px 0;
   }

   .fileBtnWrapper {
      display        : flex;
      flex-direction : row;
      align-items    : center;
      justify-content: flex-start;
      margin         : 0 0 35px 0;

      .fileBtn {
         width       : 135px;
         height      : 25px;
         font-weight : normal;
         font-size   : 14px;
         line-height : 150%;
         text-align  : center;
         color       : #FFFFFF;
         margin-right: 15px;
      }

      #fileChosen {
         font-weight: normal;
         font-size  : 14px;
         line-height: 150%;
         color      : #FFFFFF;
      }
   }

   .btnSubmit {
      width  : 240px;
      height : 50px;
      border : none;
      outline: none;
   }

   .__select {}

   .__select__title {}

   .__select__content {}

   .__select__input {}

   .__select__label {}


   .check__input {}

   .check__label {}
}