.varwinTariffs {
   margin: 0 0 100px 0;

   .sectionInner {
      position: relative;
   }

   &_title {
      font-weight: normal;
      font-size  : 36px;
      line-height: 120%;
      text-align : center;
      color      : #003399;
      margin     : 0 0 30px 0;
      width      : 55%;
   }

   &_text {
      font-weight: normal;
      font-size  : 20px;
      line-height: 150%;
      text-align : center;
      color      : #000000;
      margin     : 0 0 40px 0;
   }

   &_tarifs {
      width          : 100%;
      padding        : 0 40px;
      display        : flex;
      align-items    : center;
      justify-content: space-around;
      margin         : 0 0 100px 0;
      position       : relative;

      &-tarif {
         width           : calc(100% / 3 - 40px);
         height          : 650px;
         padding         : 23px 23px 80px 23px;
         background-color: #fff;
         box-shadow      : 0px 0px 8px rgba(0, 0, 0, 0.16), 0px 0px 21px rgba(0, 0, 0, 0.4);

         border-radius     : 12px;
         display           : flex;
         flex-direction    : column;
         align-items       : center;
         // justify-content: flex-start;
         justify-content   : space-between;

         position: relative;

         &Title {
            font-weight: 600;
            font-size  : 25px;
            line-height: 150%;
            color      : #003399;
            min-height : 75px;
            // margin: 0 0 5px 0 ;
         }

         &Description {
            font-weight: normal;
            font-size  : 13px;
            line-height: 150%;
            color      : #646464;
            min-height : 25px;
            // margin: 0 0 30px 0 ;
         }

         .restrictions {
            width: 100%;
            color: #003399;
         }

         &Restrictions {
            font-weight        : normal;
            font-size          : 17px;
            line-height        : 150%;
            color              : #003399;
            list-style         : none;
            list-style-position: inside;
            min-height         : 150px;

            // margin: 0 0 20px 0 ;
            .item {
               display: flex;

               &:before {
                  content  : '\2022';
                  display  : block;
                  font-size: 25px;
                  margin   : 0 5px 0 0;
               }
            }
         }

         &Bonus {
            font-weight: normal;
            font-size  : 15px;
            line-height: 150%;
            text-align : center;
            color      : #003399;
            min-height : 60px;
            // margin: 0 0 25px 0 ;
         }

         &Cost {
            font-weight: 600;
            font-size  : 24px;
            line-height: 150%;
            color      : #003399;
            // margin: 0 0 25px 0 ;
         }

         &Btn {
            width : 100%;
            height: 48px;
         }

         &:nth-child(2) {
            background: $primary-color url(../img/png/tarifBg.png) 50% 50%/cover no-repeat;
            color     : #fff;

            .varwinTariffs_tarifs-tarifTitle {
               color: #fff;
            }

            .varwinTariffs_tarifs-tarifDescription {
               color: #D2D2D2;
            }

            .varwinTariffs_tarifs-tarifRestrictions {
               color: #fff;
            }

            .varwinTariffs_tarifs-tarifBonus {
               color: #fff;
            }

            .varwinTariffs_tarifs-tarifCost {
               color: #fff;
            }

            .varwinTariffs_tarifs-tarifBtn {
               color: #fff;
            }

            .twoStarTip {
               color: #fff
            }

            .restrictions {
               color: #fff;
            }
         }

         .twoStarTip {
            position   : absolute;
            bottom     : 20px;
            left       : 23px;
            color      : #003399;
            font-size  : 14px;
            line-height: 150%;
            height     : 42px;
         }

         &:hover {
            box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.4), 0px 0px 21px rgba(0, 0, 0, 0.6);
         }
      }
   }

   .tipNDS {
      position   : absolute;
      bottom     : -50px;
      left       : 40px;
      font-weight: normal;
      font-size  : 20px;
      line-height: 150%;
      color      : #000;
   }

   &_btn {
      width : 277px;
      height: 48px;
   }
}