.varwinOpportunities {
   padding: 100px 0;

   .sectionInner {}

   .sectionTitle {}

   &Line {
      width                : 100%;
      // display: flex;
      // flex-direction: row;
      // align-items: center;
      // justify-content: space-between;
      display              : grid;
      gap                  : 30px;
      grid-template-columns: repeat(3, 1fr);

      &-card {
         display        : block;
         background     : #FFFFFF;
         box-shadow     : 0px 4px 20px rgba(0, 0, 0, 0.25);
         border-radius  : 12px;
         width          : 100%;
         color          : #000;
         text-decoration: none;
         padding        : 0 0 60px 0;
         margin         : 0 0 10px 0;
         transition     : 0.5s;


         &:active,
         &:visited {
            color: inherit;
         }

         .imgWrapper {
            width        : 100%;
            height       : 200px;
            border-radius: 12px;
            position     : relative;
            margin       : 0 0 25px 0;

            .cardImg {
               width        : 100%;
               height       : 100%;
               border-radius: 12px;
            }

            &::after {
               content      : '';
               display      : block;
               position     : absolute;
               top          : 0;
               left         : 0;
               width        : 100%;
               height       : 100%;
               background   : linear-gradient(223.59deg, rgba(15, 175, 252, 0) 24.39%, rgba(120, 57, 200, 0.3) 75.61%);
               border-radius: 12px;
            }
         }

         .cardTitle {
            font-size  : 21px;
            font-weight: bold;
            color      : #000;
            margin     : 0 25px 25px 25px;
            transition : 0.5s;
            position   : relative;
         }

         .cardText {
            margin: 0 25px;

         }

         &:hover {
            box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.16), 0px 0px 21px rgba(0, 0, 0, 0.4);

            .cardTitle {
               color          : $card_title_hover;
               text-decoration: underline;
            }
         }
      }
   }

   &Btn {
      text-decoration: none;
      margin         : 30px 0 0 0;
      width          : 270px;
      height         : 48px;
   }
}