.rew_itemslide {
   width          : 100%;
   display        : flex;
   align-items    : flex-start;
   justify-content: space-between;
   color          : $review_text;
   padding        : 40px;

   .rew_userInfo {
      display       : flex;
      flex-direction: column;
      line-height   : 150%;
      text-align    : center;
      font-size     : 13px;
      margin        : 0 30px 0 0;
      font-style: italic;

      img {
         margin-bottom: 20px;
         width        : 170px;
      }

      &-name {
         font-size    : 16px;
         font-weight  : bold;
         margin-bottom: 10px;
      }

      &-position {
         margin-bottom: 10px;

      }

      &-company {}
   }

   .rew_it_text {
      font-style : italic;
      font-size  : 16px;
      font-weight: bold;
      line-height: 1.5;
      p{
         margin-bottom: 10px;
      }
   }
}