* {
   font-family: sans-serif;
   margin     : 0;
   padding    : 0;
   box-sizing : border-box;
}

.sectionInner {
   width : 1170px;
   margin: 0 auto;

   display        : flex;
   flex-direction : column;
   align-items    : center;
   justify-content: flex-start;
}

.btn {
   display        : flex;
   align-items    : center;
   justify-content: center;
   // width: 240px;
   // height: 48px;
   color          : $primary_text;
   background     : linear-gradient(65.55deg, #003399 -24.66%, #00CCFF 103.65%), #F1F1F7;
   border-radius  : 24px;
   font-size      : 18px;
   text-decoration: none;

}

.hoverShanow {
   transition: 0.2s;

   &:hover {
      box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.16), 0px 0px 21px rgba(0, 0, 0, 0.4);
   }
}

.edu {
   background: linear-gradient(266.56deg, #7E3697 -2.18%, #DB2087 96.47%);
}

.sectionTitle {
   color      : $primary_color_title;
   font-size  : 36px;
   font-weight: normal;
   margin     : 0 0 50px 0;
   width      : 740px;
}

/*чекбокс*/
.check__label {
   vertical-align: middle;
   color         : #fff;

   &:before {
      content          : '';
      display          : inline-block;
      width            : 16px;
      height           : 16px;
      // padding-bottom: 0.05em;
      font-size        : 13px;
      border           : 2px solid #fff;
      border-radius    : 2px;
      vertical-align   : middle;
      margin-right     : 10px;
      text-align       : center;
      position         : relative;
      // top           : -0.2em;
   }

   a,
   a:visited,
   a:active {
      color          : #00CCFF;
      text-decoration: none;
   }
}

.check__input {
   position: absolute;
   left    : -99999999px;

   &:checked+.check__label::before {
      content: url('../img/svg/check.svg');
      color  : #00CCFF;
   }
}


/*селекты кастомные*/
.__select {
   position       : relative;
   width          : 100%;
   height         : 48px;
   margin         : 0 auto;
   border         : 2px solid #fff;
   border-radius  : 24px;
   padding        : 10px 20px;
   font-weight    : normal;
   font-size      : 16px;
   line-height    : 150%;
   color          : #FFFFFF;
   display        : flex;
   flex-direction : column;
   align-items    : center;
   justify-content: flex-start;

   &[data-state=active] &__title::before {
      transform: translate(-1px, -50%) rotate(-45deg);
   }

   &[data-state=active] &__title::after {
      transform: translate(3px, -50%) rotate(45deg);
   }

   &[data-state=active] &__content {
      opacity   : 1;
      // display: flex;
      display   : block;
      max-height: 225px;
      overflow-y: scroll;
   }

   &[data-state=active] &__label+&__input+&__label {
      max-height      : 40px;
      border-top-width: 1px;
   }

   &__title {
      display    : flex;
      align-items: center;
      width      : 100%;
      height     : 100%;
      cursor     : pointer;
      white-space: nowrap;

      &::before,
      &::after {
         content         : "";
         position        : absolute;
         top             : 50%;
         right           : 24px;
         display         : block;
         width           : 6px;
         height          : 2px;
         transition      : all 0.3s ease-out;
         background-color: #fff;
         transform       : translate(-1px, -50%) rotate(45deg);
      }

      &::after {
         transform: translate(3px, -50%) rotate(-45deg);
      }

      &:hover {
         // border-color: #5897fb;
      }

      &:hover::before,
      &:hover::after {
         // background-color: #5897fb;
      }
   }

   &__content {
      position        : absolute;
      top             : 48px;
      left            : -1px;
      display         : none;
      flex-direction  : column;
      width           : 250px;
      background-color: #ffffff;
      border          : 1px solid #c7ccd1;
      border-radius   : 24px;
      transition      : all 0.3s ease-out;
      opacity         : 0;
      z-index         : 8;
      color           : #000;

      &::-webkit-scrollbar {
         display: none;
      }

      /* Скрываем scrollbar для IE, Edge и Firefox */

      -ms-overflow-style: none;
      /* IE и Edge */
      scrollbar-width   : none;
      /* Firefox */

   }

   &__input {
      display: none;

      &:checked+label {
         background-color: #dedede;
      }

      &:disabled+label {
         opacity       : 0.6;
         pointer-events: none;
      }
   }

   &__label {
      display    : flex;
      align-items: center;
      width      : auto;
      height     : 40px;
      padding    : 0 16px;
      transition : all 0.2s ease-out;
      cursor     : pointer;
      overflow   : hidden;

      &+input+& {
         border-top: 0 solid #C7CCD160;
      }

      &:hover {
         border         : none;
         color          : #0FAFFC;
         text-decoration: underline;
      }
   }
}