.varwinInBusiness {
   padding: 105px 0 95px 0;
   margin : 0 0 60px 0;

   .sectionInner {
      // display: flex;
      // flex-direction: column;
      // align-items: center;
      // justify-content: flex-start;
   }

   &Title {}

   &_lineBlock {
      width             : 100%;
      // display: flex;
      // flex-direction: row;
      // align-items: center;
      // justify-content: space-between;

      display              : grid;
      gap                  : 30px;
      grid-template-columns: repeat(3, 1fr);

      &-card {
         position     : relative;
         width        : 100%;
         height       : 300px;
         margin       : 0 0 20px 0;
         border-radius: 12px;
         transition   : 0.5s;

         .imgWrapper {
            position     : relative;
            border-radius: 12px;
            width        : 100%;
            height       : 100%;
            overflow     : hidden;

            .cardImg {
               width        : 100%;
               height       : 100%;
               border-radius: 12px;
            }

            &::before {
               border-radius: 12px;
               content      : '';
               display      : block;
               position     : absolute;
               width        : 100%;
               height       : 100%;
               top          : 0;
               left         : 0;
               background   : radial-gradient(61.43% 61.43% at 50% 50%,
                     rgba(255, 255, 255, 0) 0%,
                     rgba(0, 0, 0, 0.35) 100%),
                  linear-gradient(256.56deg,
                     rgba(15, 175, 252, 0.15) 1.09%,
                     rgba(120, 57, 200, 0.15) 73.44%);
            }

            &::after {
               content        : 'Подробнее';
               font-size      : 14px;
               font-weight    : bold;
               color          : $primary_text;
               text-decoration: underline;
               position       : absolute;
               top            : 0;
               left           : 0;
               width          : 100%;
               height         : 100%;
               display        : flex;
               align-items    : center;
               justify-content: center;
               background     : linear-gradient(65.55deg, rgba(0, 51, 153, 0.6) -24.66%, rgba(0, 204, 255, 0.6) 103.65%);
               border-radius  : 12px;
               transition     : 0.5s;
               opacity        : 0;
            }

         }

         .imgCaption {
            width   : 240px;
            height  : 48px;
            position: absolute;
            bottom  : -24px;
            left    : calc(50% - 120px);
         }

         &:hover {
            box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.16), 0px 0px 21px rgba(0, 0, 0, 0.4);

            .imgWrapper {
               &::after {
                  opacity: 1;
               }

            }
         }
      }
   }
}