.varwinLicenseBanner {
   background     : transparent url(../img/png/licenseBanner.png) 50% 100% no-repeat;
   background-size: 100% 338px;
   display        : flex;
   align-items    : flex-end;

   .sectionInner {
      display        : flex;
      flex-direction : row;
      align-items    : center;
      justify-content: space-between;
      height         : 338px;
   }

   &_img {
      width          : 50%;
      height         : 100%;
      align-self     : end;
      display        : flex;
      align-items    : flex-end;
      justify-content: center;

      img {
         height: 120%;
      }
   }

   &_textWrapper {
      height         : 100%;
      width          : 570px;
      display        : flex;
      flex-direction : column;
      align-items    : flex-start;
      justify-content: center;

      &-title {
         font-weight  : normal;
         font-size    : 36px;
         line-height  : 150%;
         color        : #fff;
         margin-bottom: 25px;
      }

      &-text {
         font-weight  : normal;
         font-size    : 18px;
         line-height  : 150%;
         color        : #fff;
         margin-bottom: 30px;
      }

      &-Btn {
         width          : 300px;
         height         : 48px;
         font-weight    : 600;
         font-size      : 16px;
         line-height    : 100%;
         color          : #fff;
         text-decoration: none;
      }
   }

   .btn {}
}